  <tj-range v-model="item.value">
    <div class="mr_10" slot="start" v-if="item.start">{{ item.start }}</div>
    <div class="ml_10" slot="end" v-if="item.end">{{ item.end }}</div>
  </tj-range>
</div>

</div>
