  <div class="ml_10" slot="end" v-if="range.end">{{ range.end }}</div>
</tj-range>
</div>
<demo-block>
                    <div slot="desc"><tj-range v-model="range.value" showlist :min="range.min || 0" :max="range.max || 100" :step="range.step || 1" :bar-height="range.barHeight || 1" :disabled="range.disabled">
